/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import {
	BaseControl,
	TextControl,
	SelectControl,
} from '@safe-wordpress/components';
import { useInstanceId } from '@safe-wordpress/compose';
import { _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import classnames from 'classnames';
import { ErrorText } from '@nab/components';
import type { SREditProps } from '@nab/types';

/**
 * Internal dependencies
 */
import './edit.scss';
import type { Attributes } from './types';

export const Edit = ( {
	attributes: { condition, value },
	errors,
	setAttributes,
}: SREditProps< Attributes > ): JSX.Element => {
	const instanceId = useInstanceId( Edit );
	const valueControlId = `nab-referring-url-segmentation-rule__value-${ instanceId }`;

	return (
		<>
			<SelectControl
				label={ _x( 'Condition', 'text', 'nelio-ab-testing' ) }
				options={ [
					{
						value: 'is-equal-to',
						label: _x( 'Is equal to', 'text', 'nelio-ab-testing' ),
					},
					{
						value: 'is-not-equal-to',
						label: _x(
							'Is not equal to',
							'text',
							'nelio-ab-testing'
						),
					},
					{
						value: 'contains',
						label: _x( 'Contains', 'text', 'nelio-ab-testing' ),
					},
					{
						value: 'does-not-contain',
						label: _x(
							'Does not contain',
							'text',
							'nelio-ab-testing'
						),
					},
				] }
				value={ condition }
				onChange={ ( newCondition ) =>
					setAttributes( { condition: newCondition } )
				}
			/>

			<BaseControl
				id={ valueControlId }
				className={ classnames( {
					'nab-segmentation-rule__field--has-errors': errors.value,
				} ) }
				label={ _x( 'Value', 'text', 'nelio-ab-testing' ) }
				help={ <ErrorText value={ errors.value } /> }
			>
				<div className="nab-referring-url-segmentation-rule">
					<TextControl
						id={ valueControlId }
						className="nab-referring-url-segmentation-rule__value"
						value={ value }
						placeholder={ _x(
							'Referring URL value',
							'text',
							'nelio-ab-testing'
						) }
						onChange={ ( newValue ) =>
							setAttributes( { value: newValue } )
						}
					/>
				</div>
			</BaseControl>
		</>
	);
};
