/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import {
	BaseControl,
	Button,
	Dashicon,
	TextControl,
	SelectControl,
} from '@safe-wordpress/components';
import { useInstanceId } from '@safe-wordpress/compose';
import { _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import classnames from 'classnames';
import { trim, uniq, without } from 'lodash';
import { ErrorText } from '@nab/components';
import type { SREditProps } from '@nab/types';

/**
 * Internal dependencies
 */
import './edit.scss';
import type { Attributes } from './types';

export const Edit = ( {
	attributes: { condition, values, currentValue },
	errors,
	setAttributes,
}: SREditProps< Attributes > ): JSX.Element => {
	const instanceId = useInstanceId( Edit );
	const ips = currentValue
		.split( ',' )
		.map( trim )
		.filter( ( x ) => !! x.length );

	return (
		<>
			<SelectControl
				label={ _x( 'Condition', 'text', 'nelio-ab-testing' ) }
				options={ [
					{
						value: 'is-equal-to',
						label: _x( 'Is equal to', 'text', 'nelio-ab-testing' ),
					},
					{
						value: 'is-not-equal-to',
						label: _x(
							'Is not equal to',
							'text',
							'nelio-ab-testing'
						),
					},
				] }
				value={ condition }
				onChange={ ( newCondition ) =>
					setAttributes( { condition: newCondition } )
				}
			/>

			<div className="nab-ip-address-segmentation-rule">
				<BaseControl
					id={ `nab-ip-address-segmentation-rule__value-${ instanceId }` }
					className={ classnames( {
						'nab-ip-address-segmentation-rule__value': true,
						'nab-segmentation-rule__field--has-errors':
							errors.currentValue,
					} ) }
					label={ _x(
						'New IP Addresses',
						'text',
						'nelio-ab-testing'
					) }
				>
					<TextControl
						id={ `nab-ip-address-segmentation-rule__value-${ instanceId }` }
						value={ currentValue }
						placeholder={ _x(
							'IP Addresses (comma separated)',
							'text',
							'nelio-ab-testing'
						) }
						onChange={ ( newValue ) =>
							setAttributes( { currentValue: newValue } )
						}
					/>
				</BaseControl>

				<Button
					variant="secondary"
					className="nab-ip-address-segmentation-rule__action"
					disabled={ ! currentValue || !! errors.currentValue }
					onClick={ () =>
						setAttributes( {
							values: uniq( [ ...values, ...ips ] ),
							currentValue: '',
						} )
					}
				>
					{ _x( 'Add', 'command', 'nelio-ab-testing' ) }
				</Button>
			</div>

			{ !! errors.currentValue && (
				<p>
					<ErrorText value={ errors.currentValue } />
				</p>
			) }

			{ values.length >= 1 && (
				<BaseControl
					id={ `nab-ip-address-list-${ instanceId }` }
					className={ classnames( {
						'nab-ip-address-list': true,
						'nab-segmentation-rule__field--has-errors':
							errors.values,
					} ) }
					label={ _x(
						'List of IP addresses:',
						'text',
						'nelio-ab-testing'
					) }
					help={ <ErrorText value={ errors.values } /> }
				>
					<ul className="nab-ip-address-list__items">
						{ values.map( ( ip, index ) => (
							<li
								className="nab-ip-address-list__item"
								key={ index }
							>
								<code className="nab-ip-address-list__item-name">
									{ ip }
								</code>
								<Button
									className="nab-ip-address-list__item-action"
									title={ _x(
										'Remove',
										'command',
										'nelio-ab-testing'
									) }
									variant="link"
									isDestructive
									onClick={ () => {
										setAttributes( {
											values: without(
												values,
												values[ index ] ?? ''
											),
										} );
									} }
								>
									<Dashicon icon="no" />
								</Button>
							</li>
						) ) }
					</ul>
				</BaseControl>
			) }
		</>
	);
};
