/**
 * WordPress dependencies
 */
import { _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import { trim } from 'lodash';
import { isEmpty } from '@nab/utils';

/**
 * Internal dependencies
 */
import type { Attributes } from './types';

const NRE = '(25[0-5]|2[0-4][0-9]|1[0-9]{2}|0?[0-9]?[0-9]|\\*)';
const IP_REGEXP = new RegExp( `^${ NRE }\\.${ NRE }\\.${ NRE }\\.${ NRE }$` );

export function validate( {
	values,
	currentValue,
}: Attributes ): Partial< Record< keyof Attributes, string > > {
	if ( currentValue.includes( ',' ) ) {
		const ips = currentValue
			.split( ',' )
			.map( trim )
			.filter( ( x ) => !! x.length );

		if ( ! ips.every( isValidIP ) ) {
			return {
				currentValue: _x(
					'Please make sure all IP addresses are valid',
					'user',
					'nelio-ab-testing'
				),
			};
		} //end if

		return {};
	} //end if

	if ( currentValue.length && ! isValidIP( currentValue ) ) {
		return {
			currentValue: _x(
				'Please write a valid IP address',
				'user',
				'nelio-ab-testing'
			),
		};
	} //end if

	if (
		currentValue.length &&
		isValidIP( currentValue ) &&
		values.includes( currentValue )
	) {
		return {
			currentValue: _x(
				'Duplicated IP address',
				'text',
				'nelio-ab-testing'
			),
		};
	} //end if

	if ( isEmpty( values ) ) {
		return {
			values: _x(
				'Please add an IP address',
				'user',
				'nelio-ab-testing'
			),
		};
	} //end if

	return {};
} //end validate()

function isValidIP( ipAddress: string ) {
	return IP_REGEXP.test( ipAddress );
} //end isValidIP()
