/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import { sprintf, _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import interpolateComponents from '@automattic/interpolate-components';
import { isEmpty } from '@nab/utils';
import type { SRViewProps } from '@nab/types';

/**
 * Internal dependencies
 */
import type { Attributes } from './types';

export const View = ( {
	attributes: { condition, values },
}: SRViewProps< Attributes > ): JSX.Element => (
	<>
		{ interpolateComponents( {
			mixedString: sprintf(
				getLabel( condition, values ),
				wrapValues( values )
			),
			components: { code: <code /> },
		} ) }
	</>
);

function getLabel(
	condition: Attributes[ 'condition' ],
	values: Attributes[ 'values' ]
) {
	if ( isEmpty( values ) ) {
		return _x(
			'Visitor’s IP address to be defined.',
			'text',
			'nelio-ab-testing'
		);
	} //end if

	return 'is-equal-to' === condition
		? getEqualityLabel( values )
		: getDifferenceLabel( values );
} //end getLabel()

function getEqualityLabel( values: ReadonlyArray< string > ) {
	const wildcard = usesWildcard( values );

	if ( values.length === 1 && wildcard ) {
		/* translators: an IP address */
		return _x(
			'Visitor’s IP address matches %s.',
			'text',
			'nelio-ab-testing'
		);
	} //end if

	if ( values.length === 1 && ! wildcard ) {
		/* translators: an IP address */
		return _x( 'Visitor’s IP address is %s.', 'text', 'nelio-ab-testing' );
	} //end if

	if ( wildcard ) {
		/* translators: list of IP addresses */
		return _x(
			'Visitor’s IP address matches any of: %s.',
			'text',
			'nelio-ab-testing'
		);
	}

	/* translators: list of IP addresses */
	return _x(
		'Visitor’s IP address is any of: %s.',
		'text',
		'nelio-ab-testing'
	);
} //end getEqualityLabel()

function getDifferenceLabel( values: ReadonlyArray< string > ) {
	const wildcard = usesWildcard( values );

	if ( values.length === 1 && wildcard ) {
		/* translators: an IP address */
		return _x(
			'Visitor’s IP address does not match %s.',
			'text',
			'nelio-ab-testing'
		);
	} //end if

	if ( values.length === 1 && ! wildcard ) {
		/* translators: an IP address */
		return _x(
			'Visitor’s IP address is not %s.',
			'text',
			'nelio-ab-testing'
		);
	} //end if

	if ( wildcard ) {
		/* translators: list of IP addresses */
		return _x(
			'Visitor’s IP address does not match any of: %s.',
			'text',
			'nelio-ab-testing'
		);
	}

	/* translators: list of IP addresses */
	return _x(
		'Visitor’s IP address is not any of: %s.',
		'text',
		'nelio-ab-testing'
	);
} //end getDifferenceValues()

function usesWildcard( values: ReadonlyArray< string > ) {
	return values.some( ( value ) => value.includes( '*' ) );
} //end usesWildcard()

function wrapValues( values: ReadonlyArray< string > ) {
	if ( isEmpty( values ) ) {
		return '';
	} //end if
	return '{{code}}' + values.join( '{{/code}}, {{code}}' ) + '{{/code}}';
} //end wrapValues()
