/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import { sprintf, _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import interpolateComponents from '@automattic/interpolate-components';
import { isEmpty } from '@nab/utils';
import type { SRViewProps } from '@nab/types';

/**
 * Internal dependencies
 */
import type { Attributes } from './types';

export const View = ( {
	attributes: { name, condition, value },
}: SRViewProps< Attributes > ): JSX.Element => {
	if ( isEmpty( name ) ) {
		return (
			<>
				{ _x(
					'URL parameter to be defined.',
					'text',
					'nelio-ab-testing'
				) }
			</>
		);
	} //end if

	if ( isEmpty( value ) && -1 === condition.indexOf( 'exist' ) ) {
		return (
			<>
				{ _x(
					'URL parameter’s value to be defined.',
					'text',
					'nelio-ab-testing'
				) }
			</>
		);
	} //end if

	return (
		<>
			{ condition === 'is-equal-to' &&
				interpolateComponents( {
					mixedString: sprintf(
						/* translators: 1 - a parameter name, 2 - a text value */
						_x(
							'URL parameter %1$s is equal to %2$s.',
							'text',
							'nelio-ab-testing'
						),
						`{{code}}${ name }{{/code}}`,
						`{{code}}${ value }{{/code}}`
					),
					components: { code: <code /> },
				} ) }

			{ condition === 'is-not-equal-to' &&
				interpolateComponents( {
					mixedString: sprintf(
						/* translators: 1 - a parameter name, 2 - a text value */
						_x(
							'URL parameter %1$s is not equal to %2$s.',
							'text',
							'nelio-ab-testing'
						),
						`{{code}}${ name }{{/code}}`,
						`{{code}}${ value }{{/code}}`
					),
					components: { code: <code /> },
				} ) }

			{ condition === 'contains' &&
				interpolateComponents( {
					mixedString: sprintf(
						/* translators: a CSS selector, such as "a.button" */
						_x(
							'URL parameter %1$s contains %2$s.',
							'text',
							'nelio-ab-testing'
						),
						`{{code}}${ name }{{/code}}`,
						`{{code}}${ value }{{/code}}`
					),
					components: { code: <code /> },
				} ) }

			{ condition === 'does-not-contain' &&
				interpolateComponents( {
					mixedString: sprintf(
						/* translators: 1 - a parameter name, 2 - a text value */
						_x(
							'URL parameter %1$s does not contain %2$s.',
							'text',
							'nelio-ab-testing'
						),
						`{{code}}${ name }{{/code}}`,
						`{{code}}${ value }{{/code}}`
					),
					components: { code: <code /> },
				} ) }

			{ condition === 'exists' &&
				interpolateComponents( {
					mixedString: sprintf(
						/* translators: a parameter name */
						_x(
							'URL parameter %s exists.',
							'text',
							'nelio-ab-testing'
						),
						`{{code}}${ name }{{/code}}`
					),
					components: { code: <code /> },
				} ) }

			{ condition === 'does-not-exist' &&
				interpolateComponents( {
					mixedString: sprintf(
						/* translators: a parameter name */
						_x(
							'URL parameter %s does not exist.',
							'text',
							'nelio-ab-testing'
						),
						`{{code}}${ name }{{/code}}`
					),
					components: { code: <code /> },
				} ) }
		</>
	);
};
