/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import { sprintf, _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import interpolateComponents from '@automattic/interpolate-components';
import { isEmpty, isSingleton } from '@nab/utils';
import type { SRViewProps } from '@nab/types';

/**
 * Internal dependencies
 */
import type { Attributes } from './types';

export const View = ( {
	attributes: { condition, values },
}: SRViewProps< Attributes > ): JSX.Element => (
	<>
		{ isEmpty( values ) &&
			_x( 'Day of visit to be defined.', 'text', 'nelio-ab-testing' ) }

		{ condition === 'is-equal-to' &&
			isSingleton( values ) &&
			interpolateComponents( {
				mixedString: sprintf(
					/* translators: a week day name */
					_x( 'Day of visit is %s.', 'text', 'nelio-ab-testing' ),
					`{{strong}}${ values[ 0 ].label }{{/strong}}`
				),
				components: { strong: <strong /> },
			} ) }

		{ condition === 'is-equal-to' &&
			values.length > 1 &&
			interpolateComponents( {
				mixedString: sprintf(
					/* translators: a list of week day names */
					_x(
						'Day of visit is any of: %s.',
						'text',
						'nelio-ab-testing'
					),
					`{{strong}}${ values
						.map( ( l ) => l.label )
						.join( ', ' ) }{{/strong}}`
				),
				components: { strong: <strong /> },
			} ) }

		{ condition === 'is-not-equal-to' &&
			isSingleton( values ) &&
			interpolateComponents( {
				mixedString: sprintf(
					/* translators: a week day name */
					_x( 'Day of visit is not %s.', 'text', 'nelio-ab-testing' ),
					`{{strong}}${ values[ 0 ].label }{{/strong}}`
				),
				components: { strong: <strong /> },
			} ) }

		{ condition === 'is-not-equal-to' &&
			values.length > 1 &&
			interpolateComponents( {
				mixedString: sprintf(
					/* translators: a list of week day names */
					_x(
						'Day of visit is none of: %s.',
						'text',
						'nelio-ab-testing'
					),
					`{{strong}}${ values
						.map( ( l ) => l.label )
						.join( ', ' ) }{{/strong}}`
				),
				components: { strong: <strong /> },
			} ) }
	</>
);
