/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import {
	BaseControl,
	TextControl,
	SelectControl,
} from '@safe-wordpress/components';
import { useInstanceId } from '@safe-wordpress/compose';
import { _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import classnames from 'classnames';
import { ErrorText } from '@nab/components';
import type { SREditProps } from '@nab/types';

/**
 * Internal dependencies
 */
import './edit.scss';
import type { Attributes } from './types';

export const Edit = ( {
	attributes: { name, condition, value },
	errors,
	setAttributes,
}: SREditProps< Attributes > ): JSX.Element => {
	const instanceId = useInstanceId( Edit );
	const nameControlId = `nab-query-parameter-segmentation-rule__name-${ instanceId }`;
	const valueControlId = `nab-query-parameter-segmentation-rule__value-${ instanceId }`;

	return (
		<>
			<BaseControl
				id={ nameControlId }
				className={ classnames( {
					'nab-segmentation-rule__field--has-errors': errors.name,
				} ) }
				label={ _x( 'URL Parameter', 'text', 'nelio-ab-testing' ) }
				help={ <ErrorText value={ errors.name } /> }
			>
				<div className="nab-query-parameter-segmentation-rule">
					<TextControl
						id={ nameControlId }
						className="nab-query-parameter-segmentation-rule__name"
						value={ name }
						placeholder={ _x(
							'Parameter name',
							'text',
							'nelio-ab-testing'
						) }
						onChange={ ( newName ) =>
							setAttributes( { name: newName } )
						}
					/>
				</div>
			</BaseControl>

			<SelectControl
				label={ _x( 'Condition', 'text', 'nelio-ab-testing' ) }
				options={ [
					{
						value: 'is-equal-to',
						label: _x( 'Is equal to', 'text', 'nelio-ab-testing' ),
					},
					{
						value: 'is-not-equal-to',
						label: _x(
							'Is not equal to',
							'text',
							'nelio-ab-testing'
						),
					},
					{
						value: 'contains',
						label: _x( 'Contains', 'text', 'nelio-ab-testing' ),
					},
					{
						value: 'does-not-contain',
						label: _x(
							'Does not contain',
							'text',
							'nelio-ab-testing'
						),
					},
					{
						value: 'exists',
						label: _x( 'Exists', 'text', 'nelio-ab-testing' ),
					},
					{
						value: 'does-not-exist',
						label: _x(
							'Does not exist',
							'text',
							'nelio-ab-testing'
						),
					},
				] }
				value={ condition }
				onChange={ ( newCondition ) =>
					setAttributes( { condition: newCondition } )
				}
			/>

			{ 'exists' !== condition && 'does-not-exist' !== condition && (
				<BaseControl
					id={ valueControlId }
					className={ classnames( {
						'nab-segmentation-rule__field--has-errors':
							errors.value,
					} ) }
					label={ _x( 'Value', 'text', 'nelio-ab-testing' ) }
					help={ <ErrorText value={ errors.value } /> }
				>
					<div className="nab-query-parameter-segmentation-rule">
						<TextControl
							id={ valueControlId }
							className="nab-query-parameter-segmentation-rule__value"
							value={ value }
							placeholder={ _x(
								'Parameter value',
								'text',
								'nelio-ab-testing'
							) }
							onChange={ ( newValue ) =>
								setAttributes( { value: newValue } )
							}
						/>
					</div>
				</BaseControl>
			) }
		</>
	);
};
