/**
 * WordPress dependencies
 */
import * as React from '@safe-wordpress/element';
import { sprintf, _x } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import { trim } from 'lodash';
import interpolateComponents from '@automattic/interpolate-components';
import { isEmpty } from '@nab/utils';
import type { SRViewProps } from '@nab/types';

/**
 * Internal dependencies
 */
import type { Attributes } from './types';

export const View = ( {
	attributes: { condition, value },
}: SRViewProps< Attributes > ): JSX.Element => {
	if ( isEmpty( trim( value ) ) ) {
		return <>{ _x( 'URL to be defined.', 'text', 'nelio-ab-testing' ) }</>;
	} //end if

	return (
		<>
			{ condition === 'is-equal-to' &&
				interpolateComponents( {
					mixedString: sprintf(
						/* translators: an URL */
						_x(
							'Referring URL is equal to %s.',
							'text',
							'nelio-ab-testing'
						),
						`{{code}}${ value }{{/code}}`
					),
					components: { code: <code /> },
				} ) }

			{ condition === 'is-not-equal-to' &&
				interpolateComponents( {
					mixedString: sprintf(
						/* translators: an URL */
						_x(
							'Referring URL is not equal to %s.',
							'text',
							'nelio-ab-testing'
						),
						`{{code}}${ value }{{/code}}`
					),
					components: { code: <code /> },
				} ) }

			{ condition === 'contains' &&
				interpolateComponents( {
					mixedString: sprintf(
						/* translators: a text */
						_x(
							'Referring URL contains %s.',
							'text',
							'nelio-ab-testing'
						),
						`{{code}}${ value }{{/code}}`
					),
					components: { code: <code /> },
				} ) }

			{ condition === 'does-not-contain' &&
				interpolateComponents( {
					mixedString: sprintf(
						/* translators: a text */
						_x(
							'Referring URL does not contain %s.',
							'text',
							'nelio-ab-testing'
						),
						`{{code}}${ value }{{/code}}`
					),
					components: { code: <code /> },
				} ) }
		</>
	);
};
